<template>
  <div id="scroll">
    <div
      style="margin:10px 0;"
      v-if="accountInfo"
    >
      <van-cell
        :title="$route.query.otaSourceName+'账号'"
        is-link
        :value="accountInfo.otaUserName"
        title-style='color:#89868A;'
        @click="showUp('zh')"
        value-class='c_000'
      />
      <van-cell
        title="登陆密码"
        is-link
        :value="accountInfo.otaPassWord"
        title-style='color:#89868A;'
        @click="showUp('mm')"
        value-class='c_000'
      />
      <van-cell
        v-if="$route.query.otaSource == '19'"
        title="OTA酒店id"
        is-link
        :value="accountInfo.hotelOtaCode"
        title-style='color:#89868A;'
        @click="showUp('xcId')"
        value-class='c_000'
      />
      <van-cell
        v-if="platStatus"
        value-class='qwqwqw'
      >
        <template #title>
          <div>
            <span>对接状态</span>
            <span
              v-if="accountInfo.status == '0'"
              class="f12"
            >(请联系小住客服人工处理)</span>
          </div>
          <div class="c_ff">{{ platStatus[accountInfo.status] }}</div>
        </template>
      </van-cell>
    </div>
    <div>
      <table
        width='100%'
        borderColor='#cecece'
        cellSpacing='0'
        align='center'
        bgColor='#ffffff'
        border='1'
        cellpadding="0"
        border-spacing="0"
        id="plice_table"
        class="table"
      >
        <thead>
          <th class="aaa">{{ $route.query.otaSourceName }}房型名称
            <van-icon
              v-if='accountInfo && accountInfo.hotelOtaCode'
              name="replay"
              color="#2E7BF7"
              class="refresh rotate"
              @click="reload"
            />
          </th>
          <th class="bbb">预留本地房型名称</th>
        </thead>
        <tbody
          v-for="(item,i) in scaleList"
          :key="i"
        >
          <tr>
            <td style="color:#8A888B;">
              <div
                class="td_left"
                @click="openLeft(item)"
              >
                <div>{{ item.scaleName }}</div>
                <van-icon name="arrow" />
              </div>
            </td>
            <td>
              <div
                v-for="(child,j) in item.child"
                :key="j"
                class="right_scale"
                @click="openAll(child)"
              >
                <div class="flex">
                  <div class="area">&ensp;{{ j+1 }}</div>
                  <div class="center">{{ child.basicScaleName }}-{{ child.scaleName }}</div>
                  <van-icon
                    name="cross"
                    @click.stop="removeRight(child)"
                  />
                </div>
              </div>
              <div
                v-if="item.scaleName && item.otaScaleCode && item.child && item.child.length == 0 || item.scaleName && !item.otaScaleCode"
                class="plus_style"
                @click="openRight(item)"
              >
                <van-icon
                  name="plus"
                  color='#999699'
                  style="vertical-align: middle;"
                />
              </div>
            </td>
          </tr>
        </tbody>
        <!-- 添加按钮 -->
        <!-- <tbody>
          <tr>
            <td>
              <div
                class="plus_style"
                @click="addLeft('add')"
              >
                <van-icon
                  name="plus"
                  color='#999699'
                  style="vertical-align: middle;"
                />
              </div>
            </td>
            <td></td>
          </tr>
        </tbody> -->
      </table>
    </div>
    <!-- 修改账号、密码 -->
    <van-dialog
      v-model="show_confirm"
      :before-close="confirm"
      show-cancel-button
    >
      <van-field
        v-model="value"
        title-style='color:red;line-height:40px;'
        :placeholder="'请输入'+`${type == 'zh'?'账号':'密码'}`"
      />
    </van-dialog>
    <!-- 编辑、显示-左边房型名称 -->
    <van-dialog
      v-model="show_add_left_Scale"
      :before-close="confirmAddLeftScale"
      show-cancel-button
    >
      <van-field
        v-model="leftScale"
        :placeholder="'请准确填写'+`${$route.query.otaSourceName}`+'房型名称'"
      />
    </van-dialog>
    <!-- 显示-右边房型名称 -->
    <van-dialog
      v-model="show_add_right_Scale"
      title="关联房型"
      :before-close="confirmAddRightScale"
      show-cancel-button
    >
      <van-field
        v-model="leftScale"
        :label="`${$route.query.otaSourceName}`+'房型'"
        readonly
      />
      <van-field
        v-model="scaleFirst"
        @click="openScale"
        label="本地基础房型"
        readonly
        right-icon="arrow-down"
      />
      <van-field
        v-model="proFirst"
        @click="openPro"
        label="产品房型"
        readonly
        right-icon="arrow-down"
      />
    </van-dialog>
    <!-- 本地基础房型 -->
    <van-popup
      v-model='show_scale'
      position="bottom"
      style="width:100%;"
    >
      <van-picker
        title="本地基础房型"
        show-toolbar
        :columns="scaleAll"
        @confirm="onConfirmScale"
        @cancel="show_scale = false"
      />
    </van-popup>
    <!-- 产品房型 -->
    <van-popup
      v-model='show_pro'
      position="bottom"
      style="width:100%;"
    >
      <van-picker
        title="产品房型"
        show-toolbar
        :columns="proAll"
        @confirm="onConfirmPro"
        @cancel="show_pro = false"
      />
    </van-popup>
    <!-- 映射房型 -->
    <van-popup
      v-model='show_left_scale'
      position="bottom"
      class="common"
    >
      <div
        class="border_bottom"
        @click="onConfirmLeft('编辑')"
      >编辑</div>
      <div
        v-if="$route.query.otaSource == '19' && leftItem && leftItem.otaScaleCode && leftItem.child.length == 1"
        class="border_bottom"
        @click="onConfirmLeft('房态房价','otaState-page')"
      >房态房价</div>
      <div
        style="color:red;"
        class="border_bottom"
        @click="onConfirmLeft('删除')"
      >删除</div>
      <div style="height:10px;background:#F1EEF4;"></div>
      <div
        class="border_bottom"
        @click="show_left_scale = false"
      >取消</div>
    </van-popup>
  </div>
</template>
<script>
import { Toast, Dialog } from 'vant';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessVirtualRoomscaleList, GetBusinessRoomscaleByhotel } from '@/api/api_business_hotel_and_scale';
import {
    PostBusinessHotelOtaAccount,
    PostBusinessHotelOtaAccountedit,
    PostBusinessHotelOtaAccountOtaRoomscaleAdd,
    GetBusinessRoomscaleRelationByhotel,
    PostBusinessRoomscaleRelationAdd,
    PostBusinessRoomscaleRelationRemove,
    PostBusinessHotelOtaAccountOtaRoomscaleEdit,
    PostBusinessHotelOtaAccountOtaRoomscaleRemove,
    PostBusinessHotelOtaAccountOtaRoomscalerefresh
} from '@/api/api_ota';
export default {
    data() {
        return {
            accountInfo: null,
            platStatus: {},
            type: null,
            aaa: 'zzz',
            bbb: '123123123',
            value: null,
            leftScale: null,
            scaleFirst: '请选择基础房型',
            proFirst: '请选择产品房型',
            allName: null,
            leftItem: null,
            dataForm: {
                hotelId: null,
                plat: null,
                otaScaleCode: null,
                hotelOtaCode: null,
                hotelName: null,
                scaleName: null,
                roomScaleId: null,
                virtualRoomScaleId: null
            },
            scaleList: [],
            scaleAll: [],
            proAll: [],
            show_confirm: false,
            show_add_left_Scale: false,
            show_add_right_Scale: false,
            show_scale: false,
            show_pro: false,
            show_left_scale: false
        };
    },
    created() {
        this.$store.commit('setHeader', `${this.$route.query.otaSourceName}` + '房型关联');
        this.getType();
    },
    methods: {
        // 状态
        getType() {
            GetSystemDictDataByType({ dictType: 'business_ota_relation_status' }).done((res) => {
                res.data.map((item) => (this.platStatus[item.dictValue] = item.dictLabel));
                this.getLeft();
            });
        },
        reload() {
            let data = {
                hotelId: this.$route.query.hotelId,
                otaPlat: this.$route.query.otaSource
            };
            PostBusinessHotelOtaAccountOtaRoomscalerefresh(data).done((res) => {
                this.getType();
            });
        },
        // 根据ID查询OTA账户
        getLeft() {
            PostBusinessHotelOtaAccount({ hotelOtaRelationId: this.$route.query.hotelOtaRelationId }).done((res) => {
                this.accountInfo = res.data; // 账户
                this.dataForm.hotelId = this.accountInfo.hotelId;
                this.dataForm.plat = this.accountInfo.otaSource;
                this.dataForm.hotelOtaCode = this.accountInfo.hotelOtaCode;
                this.scaleList = res.order; // 房型
                if (this.scaleList.length > 0) {
                    this.getRight(this.scaleList);
                } else {
                    this.getScale();
                }
            });
        },
        // 根据酒店查询房型映射
        getRight(arr) {
            let data = {
                hotelId: this.accountInfo.hotelId,
                plat: this.accountInfo.otaSource
            };
            GetBusinessRoomscaleRelationByhotel(data).done((res) => {
                localStorage.setItem('ota_scaleList', JSON.stringify(res.data));
                arr.map((item) => {
                    item.child = [];
                    res.data.map((itm) => {
                        itm.virtualRoomScale.roomScalePlatRelationId = itm.id;
                        itm.virtualRoomScale.basicScaleName = itm.roomScale.scaleName;
                        itm.virtualRoomScale.virtualRoomScaleId = itm.virtualRoomScaleId;
                        if (item.scaleName == itm.scaleName) {
                            item.child.push(itm.virtualRoomScale);
                        }
                    });
                });
                this.getScale();
            });
        },
        // 获取-基础房型
        getScale() {
            GetBusinessRoomscaleByhotel(this.accountInfo.hotelId).done((res) => {
                this.scaleAll = res.data.map((item) => item.scaleName);
                this.scaleOldAll = res.data;
            });
        },
        // 获取-产品房型
        getPro(id) {
            let data = {
                hotelId: this.accountInfo.hotelId,
                roomScaleId: id
            };
            GetBusinessVirtualRoomscaleList(data).done((res) => {
                this.proAll = res.data.map((item) => item.scaleName);
                this.proOldAll = res.data;
                if (this.scaleFirst == '请选择基础房型') {
                    this.proFirst = '请选择产品房型';
                } else {
                    if (this.proOldAll.length > 0) {
                        this.proFirst = this.proOldAll[0].scaleName;
                        this.dataForm.virtualRoomScaleId = this.proOldAll[0].id;
                    } else {
                        this.proFirst = '请选择产品房型';
                        Toast('暂无产品房型');
                    }
                }
            });
        },
        addLeft(type) {
            this.add_or_edit_left = type;
            this.show_add_left_Scale = true;
            this.leftScale = null;
        },
        // 展开
        showUp(type) {
            this.type = type;
            if (this.type == 'zh') {
                this.value = this.accountInfo.otaUserName;
            } else if (this.type == 'mm') {
                this.value = this.accountInfo.otaPassWord;
            } else if (this.type == 'xcId') {
                this.value = this.accountInfo.hotelOtaCode;
            }
            this.show_confirm = true;
        },
        // 编辑账号、密码
        confirm(action, done) {
            if (action == 'confirm') {
                let data = {
                    id: this.accountInfo.id
                };
                if (!this.value) {
                    Toast('请输入' + `${this.type == 'zh' ? '账号' : this.type == 'mm' ? '密码' : 'OTA酒店id'}`);
                    done(false);
                    return;
                }
                if (this.type == 'zh') {
                    data.otaUserName = this.value;
                } else if (this.type == 'mm') {
                    data.otaPassWord = this.value;
                } else if (this.type == 'xcId') {
                    data.hotelOtaCode = this.value;
                }
                this.show_confirm = false;
                PostBusinessHotelOtaAccountedit(data).done((res) => {
                    done(false);
                    this.show_confirm = false;
                    this.getType();
                });
            } else if (action == 'cancel') {
                done(false);
                this.show_confirm = false;
            }
        },
        // 添加、编辑-左边的房型
        confirmAddLeftScale(action, done) {
            if (action == 'confirm') {
                if (!this.leftScale) {
                    Toast('请输入' + `${this.$route.query.otaSourceName}` + '房型名称');
                    done(false);
                    return;
                }
                done(false);
                if (this.add_or_edit_left == 'add') {
                    let data = {
                        hotelId: this.accountInfo.hotelId,
                        otaPlat: this.accountInfo.otaSource,
                        scaleName: this.leftScale
                    };
                    PostBusinessHotelOtaAccountOtaRoomscaleAdd(data).done((res) => {
                        Toast(res.msg);
                        done(false);
                        this.show_add_left_Scale = false;
                        this.getType();
                    });
                } else {
                    let data = {
                        otaRoomScaleId: this.leftItem.id,
                        scaleName: this.leftScale
                    };
                    PostBusinessHotelOtaAccountOtaRoomscaleEdit(data).done((res) => {
                        done(false);
                        this.show_add_left_Scale = false;
                        this.getType();
                    });
                }
            } else if (action == 'cancel') {
                done(false);
                this.show_add_left_Scale = false;
            }
        },
        // 展示全部名称
        openAll(item) {
            this.allName = item.basicScaleName + '-' + item.scaleName;
            this.NoOpen();
        },
        // 关闭
        NoOpen() {
            Dialog.confirm({
                message: `${this.allName}`,
                showCancelButton: false
            });
        },
        // 展开-基础房型
        openScale() {
            if (this.scaleAll.length == 0) {
                Toast('暂无基础房型');
            } else {
                this.show_scale = true;
            }
        },
        // 展开-产品房型
        openPro() {
            if (this.scaleFirst == '请选择基础房型') {
                Toast('请先选择基础房型');
            } else if (this.proAll.length == 0) {
                Toast('暂无产品房型');
            } else {
                this.show_pro = true;
            }
        },
        // 展开-右侧房型添加
        openRight(item) {
            this.scaleFirst = '请选择基础房型';
            this.proFirst = '请选择产品房型';
            this.dataForm.otaScaleCode = item.otaScaleCode;
            this.leftScale = item.scaleName;
            this.dataForm.scaleName = item.scaleName;
            this.show_add_right_Scale = true;
        },
        // 选择-本地基础房型
        onConfirmScale(e) {
            this.scaleFirst = e;
            this.dataForm.roomScaleId = this.scaleOldAll.find((item) => item.scaleName == e).id;
            this.getPro(this.dataForm.roomScaleId);
            this.show_scale = false;
        },
        // 选择-产品房型
        onConfirmPro(e) {
            this.proFirst = e;
            this.dataForm.virtualRoomScaleId = this.proOldAll.find((item) => item.scaleName == e).id;
            this.show_pro = false;
        },
        // 添加-右边的房型
        confirmAddRightScale(action, done) {
            if (action == 'confirm') {
                if (this.scaleAll.length == 0) {
                    Toast('暂无基础房型');
                    done(false);
                    return;
                }
                if (this.scaleAll.length > 0 && this.scaleFirst == '请选择基础房型') {
                    Toast('请先选择基础房型');
                    done(false);
                    return;
                }
                if (this.proAll.length == 0) {
                    Toast('暂无产品房型');
                    done(false);
                    return;
                }
                PostBusinessRoomscaleRelationAdd(this.dataForm)
                    .done((res) => {
                        done(false);
                        this.show_add_right_Scale = false;
                        this.getType();
                    })
                    .then((e) => {
                        done(false);
                        this.show_add_right_Scale = false;
                    });
            } else if (action == 'cancel') {
                done(false);
                this.show_add_right_Scale = false;
            }
        },
        // 展开-映射房型-删除、编辑
        openLeft(item) {
            this.leftItem = item;
            this.show_left_scale = true;
        },
        // 选择-映射房型
        onConfirmLeft(title, url) {
            this.show_left_scale = false;
            if (title == '编辑') {
                this.show_add_left_Scale = true;
                this.add_or_edit_left = 'edit';
                this.leftScale = this.leftItem.scaleName;
            } else if (title == '房态房价') {
                this.$router.push({
                    path: url,
                    query: {
                        sign: 'ota',
                        bookMonth: '5',
                        pro_id: '20',
                        scale_id: '2',
                        virtualRoomScaleId: this.leftItem.child[0].virtualRoomScaleId,
                        otaPlat: this.leftItem.otaPlat,
                        scaleName: this.leftItem.scaleName,
                        hotelId: this.$route.query.hotelId
                    }
                });
            } else {
                Dialog.confirm({
                    title: '提示',
                    message: `确认删除${this.leftItem.scaleName}` + '?'
                })
                    .then(() => {
                        PostBusinessHotelOtaAccountOtaRoomscaleRemove({ otaRoomScaleId: this.leftItem.id }).done((res) => {
                            this.getType();
                        });
                    })
                    .catch(() => {});
            }
        },
        // 删除本地房型
        removeRight(child) {
            Dialog.confirm({
                title: '确认删除?',
                message: child.basicScaleName + '-' + child.scaleName
            })
                .then(() => {
                    PostBusinessRoomscaleRelationRemove({ roomScalePlatRelationId: child.roomScalePlatRelationId }).done((res) => {
                        Toast(res.msg);
                        this.getType();
                    });
                })
                .catch(() => {});
        }
    }
};
</script>
<style scoped>
#scroll {
    position: fixed;
    top: 50px;
    left: 0;
    overflow: scroll;
    height: 90%;
}
.c_000 {
    color: #000;
}
.c_ff {
    color: #ff794c;
}
.f12 {
    color: red;
    font-size: 12px;
}
.qwqwqw {
    display: flex;
    justify-content: space-between;
    color: #89868a;
}
.aaa {
    display: flex;
    align-items: center;
    justify-content: center;
}
.common {
    width: 100%;
    z-index: 2010;
    line-height: 50px;
    font-size: 15px;
    color: #000;
}
.border_bottom {
    border-bottom: 1px solid #f1eef4;
}
.table {
    color: #000;
    font-size: 15px;
    table-layout: fixed; /* (允许设置列宽,通过th)  默认为 table-layout: automatic 默认每一列列宽均分(不可设置列宽) */
}
th {
    padding: 10px;
}
tr {
    color: #000;
}
td {
    border: 1px solid #ece9ec;
}
.plus_style {
    width: 22%;
    margin: 10px auto;
    line-height: 30px;
    background: #f1eff2;
    border-radius: 6px;
}
.flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 12px;
}
.area {
    width: 25px;
    height: 25px;
    background: #2f7cfa;
    border-radius: 30px 0px 100px 0px;
    position: absolute;
    top: 0;
    left: 0;
    font-size: 14px;
    display: flex;
    align-items: center;
    color: #fff;
}
.center {
    padding-left: 10px;
    width: 78%;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    max-height: 40px;
}
.right_scale {
    width: 90%;
    margin: 10px auto;
    background: #f1eef4;
    border-radius: 8px;
    padding: 10px 0;
    position: relative;
}
.rotate1 {
    　　transform: rotate(45deg);
    　　transtion: all 0.3s linear;
    　　-webkit-transform: rotate(45deg);
    　　-webkit-transtion: all 0.3s linear;
}
.rotate {
    　　transform: rotate(0);
    　　transtion: all 0.3s linear;
    　　-webkit-transform: rotate(0);
    　　-webkit-transtion: all 0.3s linear;
}
.td_left {
    display: flex;
    justify-content: center;
    align-items: center;
}
/deep/ .van-cell {
    display: flex;
    align-items: center;
}
/deep/ .van-cell__title {
    text-align: left;
}
/deep/ .van-field__control {
    line-height: 40px;
}
</style>



